.hero-section {
  display: flex;
  width: 100%;
  /* margin: 6.5rem 0; */
  /* margin: auto; */
  /* border: 1px solid violet; */
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  min-height: 100vh;
  height: 100%;
  position: relative;
  padding: 7rem 10rem;

  /* justify-items: center; */
}

.hero-section::before {
  content: "";
  /* padding: 2rem; */
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: radial-gradient(circle at bottom, #1b2735 -5%, #101010 100%);
  background-attachment: fixed;
  /* overflow: hidden; */
}
.star-container .star {
  position: absolute;
  /* width: 2px;
  height: 2px; */
  background: #fff;
  border-radius: 50%;
}

.hero-section .left {
  /* margin-right: 0; */
  padding: 2rem;
  /* padding: ; */
  /* max-width: 800%; */
  text-align: center;
  /* border: 1px solid violet; */
  margin: auto;
}

.hero-section .left h3 {
  font-size: 2rem;
  color: #999;
  padding-bottom: 1rem;
  margin: auto;
}

.hero-section .left h1 {
  font-size: 4rem;
  margin: auto;
  text-align: center;
}

.hero-section .left p {
  font-size: 1.2rem;
  margin: 2rem 0;
  margin-top: 1.2rem;
}

.hero-section .btn-container {
  display: flex;
  align-items: center;
  justify-content: center;
  /* border: 1px solid violet; */
}

.hero-section .left .btn {
  /* align-self: center; */
  margin: 1rem 0.5rem;
}

.hero-section .right {
  padding: 1rem;
  /* padding-right: 0; */
  max-width: 400px;
  /* border: 1px solid violet; */
  margin: auto;
}

.hero-section .right .img-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  max-width: 100%;
}

.hero-section .right img {
  /* height: 80vh; */
  /* max-height: 90%; */
  max-width: 100%;
  margin: auto;
  /* border: 1px solid violet; */
  /* box-shadow: 0px 0px 10px #ccc; */
  filter: drop-shadow(20px 20px 10px #101010);
  /* filter: drop-shadow(20px 20px 2px #101010) drop-shadow(20px 20px 2px #fff); */
  /* border-radius: 50%; */
}

@media screen and (max-width: 960px) {
  .hero-section .btn-container {
    flex-direction: column;
  }
  .hero-section .left .btn {
    /* align-self: center; */
    margin: 0.25rem auto;
  }
}

@media screen and (max-width: 740px) {
  .hero-section .right {
    padding: 2rem;
  }
  .hero-section .left {
    padding: 2rem;
  }
  .hero-section .left h1 {
    font-size: 3rem;
    /* margin: auto; */
  }
  .hero-section .left h3 {
    font-size: 1.5rem;
  }
  .hero-section .left p {
    font-size: 1.2rem;
  }
  .hero-section .right img {
    /* height: 60vh; */
    width: 100%;
  }
}

@media screen and (max-width: 400px) {
  .hero-section .right img {
    /* height: 60vh; */
  }
}

@media screen and (max-width: 1040px) {
  .hero-section {
    padding: 7rem 6.5rem;
    /* padding-top: 7rem; */
  }
}

@media screen and (max-width: 640px) {
  .hero-section {
    padding: 7rem 2.5rem;
    /* padding-top: 7rem; */
  }
}

@media screen and (max-width: 400px) {
  .hero-section {
    padding: 7rem 2rem;
    /* padding-top: 7rem; */
  }
}

